@import url("https://fonts.googleapis.com/css2?family=Arvo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Mono&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap");

body {
  font-family: "DM Mono", monospace;
  color: grey;
  background: #1b091c;
  font-family: "Quicksand", sans-serif;
  height: 100vh;
}

.contractStat {
  position: fixed;
  width: 100%;
  top: 0;
  background: #1b091c;
  z-index: 100;
}

.contractStat ul {
  width: 60%;
  margin: auto;
  display: flex;
  padding: 20px;
  list-style: none;
  font-weight: bold;
}

.contractStat ul li {
  flex: 1;
  text-align: center;
}

.main {
  margin: auto;
  margin-bottom: 20px;
}

/* .start{
    display:none;
} */
.mid {
  margin-top: 90px;
  background: url("./images/newwall.png");
  padding: 50px;
  padding-top: 40px;
  z-index: 1;
}

.top {
  padding: 3px;
}

.header {
  background: #2e0f2f;
  height: 50px;
  padding: 10px;
}

#historydata {
  height: 180px;
  overflow: hidden;
}

.betarea {
  padding: 40px;
  width: 450px;
  margin: auto;
  border: 9px solid #2e0f2f;
  background: #2e0f2f;
  opacity: 100;
  z-index: 2;
  position: relative;
  padding-bottom: 20px;
  padding-top: 20px;
}

.imgdata {
}

.tabs {
  height: 50px;
  width: 300px;
  margin: auto;
  display: flex;
  margin-top: 6px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: monospace;
}

button {
  background: none;
  color: grey;
  border: none;
  outline: none;
  font-weight: bold;
  outline: none;
}

*:focus {
  outline: 0 !important;
}

.tabs button {
  flex: 1;
  margin: 6px;
  border-radius: 8px;
}

.active {
  background: #8a62ec;
  color: white;
  border: 1px solid #8a62ec;
  font-family: monospace;
}

.act_tab {
  border-left: 3px solid #8a62ec;
  border-right: 3px solid #8a62ec;
}

.start {
  margin-top: 10px;
  background: #ffeb3b;
  color: #16182f;
  width: 100%;
}

.cashout {
  margin-top: 10px;
  background: #4caf50;
  color: #16182f;
  width: 100%;
  display: none;
}

.opc {
  cursor: not-allowed;
  opacity: 0.5;
}

.inputdata {
  padding-top: 10px;
  display: flex;
}

.inputdata input {
  flex: 1;
  height: 40px;
  background: #433a4e;
  border: none;
  outline: none;
  color: white;
  text-align: center;
}

.inputdata .half {
  flex: 0.4;
  text-align: center;
  padding: 10px;
  background: #6c4c7b;
  color: #c7e0f9;
  font-weight: bold;
  height: 40px;
  border-right: 1px solid #2e0f2f;
}

.winwin {
  position: absolute;
  background: green;
  width: 300px;
  margin-left: -22px;
  height: 50px;
  padding-top: 10px;
  background-color: rgba(200, 255, 255, 0.1);
  cursor: not-allowed;
  font-family: monospace;

  /* display: none; */
}

body {
  animation: anim-logo 4s ease-in-out 0s 1;
}

@keyframes anim-logo {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes anim-border {
  0% {
    transform: rotateZ(0);
  }

  100% {
    transform: rotateZ(360deg);
  }
}

.history {
  width: 700px;
}

.history ul {
  display: flex;
  list-style-type: none;
  width: 240px;
  background: black;
  margin: auto;
}

.history ul li {
  flex: 1;
  display: block;
  padding: 10px;
  width: 100px;
}

.his1 {
  color: #ecd643;
}

.historyheader {
  padding: 10px;
  text-align: center;
  display: flex;
}

.historyheader .player1 {
  font-weight: bold;
  flex: 1;
}

.datahis {
  display: flex;
  text-align: center;
  font-size: 10px;
  color: #c7e0f9;
  background: green;
  margin-bottom: 3px;
  padding-top: 6px;
  padding-bottom: 6px;
  border-radius: 20px;
  margin: auto;
  margin-bottom: 10px;
  width: 80%;
  transition: all 0.3s ease-in-out;
  transform: scale(1.3);
  font-size: 9px;
  border-radius: 13px;
}

.datahis div {
  flex: 1;
}
.datamd {
  /* display: none; Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 400px;
  text-align:center;
  background-color: black;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

@media only screen and (max-width: 700px) {
  .betarea {
    width: 320px;
    margin: auto;
    border: 9px solid #2e0f2f;
    background: #2e0f2f;
    opacity: 100;
    z-index: 2;
    position: relative;
    padding: 10px;
    text-align: center;
  }

  .winwin {
    width: 200px;
    padding-top: 6px;
    font-size: 5px;
    font-family: monospace;
  }

  .history {
    width: 100%;
  }

  .tabs {
    display: flex;
    margin-top: 3px;
    width: 200px;
    height: 40px;
    margin: auto;
  }

  .half {
    display: none;
  }

  .contractStat ul {
    width: 100%;
    margin: auto;
    display: flex;
    padding: 20px;
    list-style: none;
    font-weight: bold;
  }

  .mid {
    padding: 0;
  }
  .modal-content {
    width: 280px;
  }
}
